@font-face {
  font-family: "Nevis";
  src: url("../../../build/css/fonts/nevis.eot");
  src: url("../../../build/css/fonts/nevis.eot?#iefix") format("embedded-opentype"), url("../../../build/css/fonts/nevis.woff") format("woff"), url("../../../build/css/fonts/nevis.ttf") format("truetype"), url("../../../build/css/fonts/nevis.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "FjallaOne";
  src: url("../../../build/css/fonts/FjallaOne-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Websymbols";
  src: url("../../../build/css/fonts/websymbols-regular-webfont.svg") format("svg");
  src: url("../../../build/css/fonts/websymbols-regular-webfont.eot?#iefix") format("embedded-opentype"), url("../../../build/css/fonts/websymbols-regular-webfont.woff") format("woff"), url("../../../build/css/fonts/websymbols-regular-webfont.ttf") format("truetype"), url("../../../build/css/fonts/websymbols-regular-webfont.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-image: url("../../../build/img/paven.png");
}

#header {
  min-width: 1024px;
  width: 100%;
  height: 250px;
  margin: 0px auto;
}
#header #logotipo {
  width: 770px;
  margin: 0px auto;
}
#header #logotipo img {
  display: block;
  text-align: center;
  margin: 0px auto;
  width: 140px;
}
#header h1 {
  text-align: center;
  font-size: 40px;
  color: #70b231;
  font-family: "Nevis";
  text-transform: uppercase;
  letter-spacing: 4px;
  margin-top: 10px;
  text-shadow: 0px 2px 1px #333333;
  transition: all 0.3s;
}
#header h1:hover {
  text-shadow: 0px 2px 1px #333333, 0px 0px 4px #70b231;
}

#nav {
  width: 100%;
  height: 40px;
  background-color: #333333;
  font-family: sans-serif, Helvetica, Arial;
  font-size: 14px;
  box-shadow: 0px 0px 2px gray;
}
#nav > ul {
  list-style: none;
  text-decoration: none;
  margin: 0px auto;
  width: 1090px;
}
#nav > ul > li {
  line-height: 40px;
  float: left;
  border-right: 1px solid gray;
  transition: all 0.3s;
}
#nav > ul > li a {
  color: white;
  text-decoration: none;
  display: block;
  padding-left: 20px;
  padding-right: 20px;
}
#nav > ul > li ul {
  display: none;
  position: absolute;
  width: 160px;
  box-shadow: 0px 2px 2px gray;
  z-index: 1;
  padding: 0;
}
#nav > ul > li ul li {
  list-style: none;
  background-color: #f2f1f0;
  border-bottom: 1px solid #d6d6d6;
  padding: 8px;
  line-height: 25px;
  font-size: 13px;
  float: none;
  clear: both;
  transition: all ease-in-out 0.3s;
}
#nav > ul > li ul li a {
  color: #666;
}
#nav > ul > li ul li:hover {
  box-shadow: 0px 0px 6px #CCC inset;
}
#nav > ul > li:first-child {
  border-left: 1px solid gray;
}
#nav > ul > li:hover {
  background-color: #70b231;
  box-shadow: 0px 0px 4px green inset;
}
#nav > ul > li:hover ul {
  display: block;
}

#content {
  width: 1250px;
  margin: 0px auto;
}
#content aside {
  width: 200px;
  margin: 30px;
  background-color: white;
  border: 1px solid #ccc;
  box-shadow: 0px 2px 2px #ccc;
  font-family: sans-serif, arial, helvetica;
  float: left;
  /*Heading font color for sub-menus in aside section*/
}
#content aside .widget {
  display: block;
  width: 180px;
  margin: 5px auto;
  padding: 8px 10px;
  font-size: 14px;
  border-bottom: 1px dashed #333;
}
#content aside .widget:last-child {
  margin-bottom: 20px;
}
#content aside #login h3 {
  display: block;
  width: 100%;
  color: #333333;
  text-align: center;
  padding: 2px auto;
  background: #f2f3f2;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  color: #000;
}
#content aside #aula_virtual1 h3 {
  display: block;
  width: 100%;
  color: #333333;
  text-align: center;
  padding: 2px auto;
  background: #f2f3f2;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  color: #0c73f2;
}
#content aside #aula_virtual2 h3 {
  display: block;
  width: 100%;
  color: #333333;
  text-align: center;
  padding: 2px auto;
  background: #f2f3f2;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  color: #9b2422;
}
#content aside #enlaces_interes h3 {
  display: block;
  width: 100%;
  color: #333333;
  text-align: center;
  padding: 2px auto;
  background: #f2f3f2;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
  color: #000;
}
#content aside form input[type=text], #content aside form input[type=password] {
  border: 1px solid #c9c9c9;
  box-shadow: 0px 0px 2px #ccc inset;
  padding: 2px 1px;
  color: gray;
  transition: all 0.3s;
}
#content aside form input[type=text]:focus, #content aside form input[type=password]:focus {
  outline: none;
  border: 1px solid #70b231;
  color: black;
}
#content aside form input[type=text], #content aside form input[type=password], #content aside form label {
  display: block;
  width: 95%;
  margin: 2px 0px;
}
#content aside form button {
  display: block;
  color: white;
  border-radius: 2px;
  margin: 5px 0px 10px 0px;
  width: 60px;
  padding: 3px;
  background-color: #70b231;
  border: 1px solid green;
  cursor: pointer;
}
#content aside form button:hover {
  box-shadow: 0px 0px 2px gray;
}
#content aside form button:active {
  box-shadow: 0px 0px 3px gray inset;
  text-shadow: 0px 0px 1px #ccc;
}
#content aside a {
  display: block;
  color: #6baa2f;
  text-decoration: none;
  margin-bottom: 2px;
}
#content aside a:hover {
  color: #578a26;
}
#content aside a:last-of-type {
  margin-bottom: 10px;
}
#content aside ul {
  padding: 0px;
}
#content aside ul li {
  margin-top: 8px;
  list-style: square;
  margin: 8px 20px 20px;
}

main section {
  width: 75%;
  float: right;
  font-family: sans-serif, Arial, Helvetica;
  margin-top: 40px;
  background-color: white;
  padding: 20px;
  border: 1px solid #ccc;
  box-shadow: 0px 0px 4px #c9c9;
}
main section:last-of-type {
  margin-bottom: 40px;
}
main section .titulo {
  font-size: 24px;
  font-weight: normal;
  letter-spacing: 1px;
  text-align: center;
  margin-bottom: 5px;
  font-family: "FjallaOne";
  padding-top: 20px;
  padding-bottom: 10px;
  margin-top: -20px;
  border-bottom: 1px solid #ccc;
}
main section article {
  margin: 20px 10px;
  position: relative;
}
main section article p {
  color: #333333;
  font-size: 16px;
  overflow: hidden;
  line-height: 24px;
}
main section article .date {
  color: gray;
  font-size: 14px;
}
main section article .icon {
  font-family: "Websymbols";
  float: left;
  margin-right: 5px;
}
main section article a {
  color: #6baa2f;
  text-decoration: none;
  margin-bottom: 2px;
}
main section article a:hover {
  color: #578a26;
}
main section article a:last-of-type {
  margin-bottom: 10px;
}
main section .stickers .sticker-r {
  position: absolute;
  z-index: 0;
  margin-top: -40px;
}
main section .stickers .sticker-l {
  position: absolute;
  z-index: 0;
  left: 1245px;
  margin-top: -40px;
}

footer {
  float: none;
  clear: both;
  width: 100%;
  text-align: center;
  background: #333333;
  color: white;
  padding: 15px 0px;
  font-weight: bold;
  font-size: 18px;
  line-height: 8px;
}
footer .center {
  text-align: center;
}